import Vue from 'vue'
import { mapMutations } from 'vuex'
export default {
  methods: {
    ...mapMutations(['cerrarSesion']),
    formatNumberBySeparator (number, separator = '.') {
      return number.toString().replace(/\B(?=(\d{3})+(?!\d))/g, separator)
    },
    swalAlertError (e) {
      Vue.swal({
        title: e.response.data.title,
        text: e.response.data.text,
        icon: 'error',
      })
      if (
        // Si es un error de validación (token o rol) y el token caducó o no existe, cierra sesión
        e.response.data.code &&
        (e.response.data.code === 'token-timed-out' ||
          e.response.data.code === 'no-token' ||
          e.response.data.code === 'invalid-user')
      ) {
        this.cerrarSesion()
      }
    },
    getDateByDateTime (dateTime) {
      const date = dateTime.substring(0, dateTime.indexOf('T'))
      const dateArr = date.split('-')
      const reverseArr = dateArr.reverse()
      return reverseArr.join('-')
    },
    getTimeByDateTime (dateTime) {
      const time = dateTime.substring(dateTime.indexOf('T') + 1)
      const timeArr = time.split(':')
      return timeArr[0] + ':' + timeArr[1]
    },
    debounce (fn, delay) {
      var timeoutID = null
      return function () {
        clearTimeout(timeoutID)
        var args = arguments // Argumentos pasados a la función
        var that = this

        timeoutID = setTimeout(function () {
          fn.apply(that, args) // Invoca la función pasándole los argumentos
        }, delay)
      }
    },
  },
}
