<template>
  <div>
    <v-container class="admin-problemas__container">
      <v-row>
        <v-col>
          <show-dialog-problema
            v-bind="{ value }"
            @procesar="procesar($event)"
            @cerrar="cerrar()"
          />
          <base-material-card
            color="success"
            icon="mdi-view-carousel"
            inline
            title="Problemas"
            class="px-5 py-3 mb-5"
          >
            <v-row class="admin-problemas_row__nuevo">
              <v-col class="text-right">
                <v-tooltip bottom>
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn
                      class="success ma-0"
                      v-bind="attrs"
                      @click="abrirCrear()"
                      v-on="on"
                    >
                      Nuevo Problema
                    </v-btn>
                  </template>
                  <span>Crea un nuevo Problema</span>
                </v-tooltip>
              </v-col>
            </v-row>
            <v-simple-table class="admin-problemas__table">
              <thead>
                <tr>
                  <th>Codigo</th>
                  <th>Nombre</th>
                  <th style="text-align:right;">
                    Acciones
                  </th>
                </tr>
              </thead>

              <tbody>
                <tr
                  v-for="(item, i) in problemas"
                  :key="i"
                >
                  <td>{{ item.CodProblema }}</td>
                  <td>{{ item.NombreProblema }}</td>
                  <td class="text-right">
                    <v-tooltip bottom>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn
                          class="warning btn-edit"
                          v-bind="attrs"
                          small
                          v-on="on"
                          @click="abrirEditar(item.CodProblema)"
                        >
                          <v-icon>mdi-pencil</v-icon>
                        </v-btn>
                      </template>
                      <span>Editar</span>
                    </v-tooltip>
                    <v-tooltip bottom>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn
                          class="error btn-delete"
                          v-bind="attrs"
                          small
                          v-on="on"
                          @click="
                            eliminar(item.CodProblema, item.NombreProblema)
                          "
                        >
                          <v-icon> mdi-trash-can </v-icon>
                        </v-btn>
                      </template>
                      <span>Eliminar</span>
                    </v-tooltip>
                  </td>
                </tr>
              </tbody>
            </v-simple-table>
          </base-material-card>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
  import Vue from 'vue'
  import { mapState, mapMutations } from 'vuex'
  import swalAlertError from '@/mixins'
  export default {
    components: {
      ShowDialogProblema: () => import('@/components/base/ShowDialogProblema'),
    },
    mixins: [swalAlertError],
    data () {
      return {
        value: {
          active: false,
          problema: {
            CodProblema: '',
            NombreProblema: '',
            Descripcion: '',
            Imagen: '',
            ToLink: '',
            MetaTitle: '',
          },
          imagen: null,
          loader: false,
        },
        problemas: '',
      }
    },
    computed: {
      ...mapState(['token']),
    },
    created () {
      this.getProblemas()
    },
    methods: {
      ...mapMutations(['cerrarSesion']),
      getProblemas () {
        Vue.axios
          .post('/api/problemas')
          .then(res => {
            this.problemas = res.data.problemas
          })
          .catch(e => {
            console.log(e.response)
          })
      },
      getProblema (codProblema) {
        Vue.axios
          .post('/api/problemas/problema', { codProblema, addMetaTitle: true })
          .then(res => {
            this.value.problema = res.data.problema[0]
          })
          .catch(e => {
            console.log(e.response)
          })
      },
      abrirEditar (codProblema) {
        this.getProblema(codProblema)
        this.value.active = true
      },
      abrirCrear () {
        this.value.active = true
      },
      cerrar () {
        this.value.active = false
        this.value.problema = {}
        this.value.imagen = null
        this.value.loader = false
      },
      procesar (val) {
        console.log('procesando...')
        this.value.loader = true
        const headers = {
          'x-token': this.token,
        }
        const body = new FormData()
        body.append('CodProblema', val.problema.CodProblema)
        body.append('NombreProblema', val.problema.NombreProblema)
        body.append('Descripcion', val.problema.Descripcion)
        body.append('Imagen', val.imagen)
        body.append('ToLink', val.problema.ToLink)
        body.append('MetaTitle', val.problema.MetaTitle)
        // console.log(headers)
        if (val.problema.CodProblema) {
          this.update(body, headers)
        } else {
          this.create(body, headers)
        }
      },
      create (body, headers) {
        Vue.axios
          .post('/api/problemas/crear', body, { headers })
          .then(res => {
            this.problemas.unshift(res.data.Problema[0])
            this.cerrar()
            Vue.swal('Problema Creado', '', 'success')
          })
          .catch(e => {
            this.cerrar()
            this.swalAlertError(e)
          })
      },
      update (body, headers) {
        console.log('editando')
        Vue.axios
          .put('/api/problemas/editar', body, { headers })
          .then(res => {
            const index = this.problemas.findIndex(
              problema => problema.CodProblema === Number(res.data.CodProblema),
            )
            this.problemas[
              index
            ].NombreProblema = this.value.problema.NombreProblema
            this.cerrar()
            Vue.swal('Problema Actualizado', '', 'success')
          })
          .catch(e => {
            this.cerrar()
            this.swalAlertError(e)
          })
      },
      eliminar (CodProblema, NombreProblema) {
        Vue.swal({
          title: `Estás seguro que deseas borrar ${NombreProblema}?`,
          showConfirmButton: true,
          showCancelButton: true,
          confirmButtonText: 'Eliminar',
          cancelButtonText: 'Cancelar',
          confirmButtonColor: '#ff5252',
          cancelButtonColor: '#a1a1a1',
        }).then(result => {
          if (result.isConfirmed) {
            Vue.swal('Problema Eliminado', '', 'success')
            Vue.axios
              .delete(`/api/problemas/${CodProblema}`, {
                headers: { 'x-token': this.token },
              })
              .then(res => {
                const index = this.problemas.findIndex(
                  p => p.CodProblema === Number(res.data.CodProblema),
                )
                this.problemas.splice(index, 1)
              })
              .catch(e => {
                this.cerrar()
                this.swalAlertError(e)
              })
          }
        })
      },
    },
  }
</script>
